.signin-outer {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-image: url("../../../assets/img/background.jpg");
}

.card-signin {
  border-radius: 40px;
  width: 100%;
  background-repeat: no-repeat;
  background-position: bottom left;
  background-size: 390px;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  background-color: #ffffffbf;
}

@media (max-width: 1025px) {
  .card-signin {
    background-size: 390px;
    box-shadow: none;
    background-color: transparent;
    border: none;
  }
}

@media only screen and (max-width: 550px) {
  .card-signin {
    border-radius: 40px;
    width: 100%;
    background-repeat: no-repeat;
    background-position: bottom left;
    background-size: 390px;
    box-shadow: none;
    background-color: transparent;
    border: none;
  }

  .w-75 {
    width: 100% !important;
  }

  .card-body {
    margin: 0 auto;
    width: 90%;
  }
}

.card-signin {
  margin-top: 8rem !important;
}

.card-body {
  margin: 0 auto;
  width: 80%;
}

.in-bnt {
  margin: 2rem 0;
}

.logBtn {
  background: #f65c00;
  border: 1px solid #f65c00;
  color: #fff;
  width: 90%;
}

.int-bnt a {
  color: #2a85ff !important;
}

.int-bnt {
  font-size: 15px;
  font-weight: 600;
}